.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.logo {
  width: 100px;
  height: 100px;
  /* border-radius: 50% 50%; */
}

.nav-items > ul {
  font-size: 20px;
  display: flex;
  list-style-type: none;
}

.nav-items > ul > li {
  padding: 10px;
  margin: 10px;
}

.res-card {
  margin: 5px;
  padding: 5px;
  width: 250px;
  /* height: 290px; */
  border: 1px solid black;
  background-color: rgb(131, 209, 131);
}
.search {
  padding: 5px;
}
.res-card:hover {
  cursor: pointer;
  border-radius: 10px solid black;
}
.res-card p {
  display: grid;
}
.res-card > span {
  display: flex;
  justify-content: space-evenly;
  margin-top: -19px;
}
.res-card > span > h4 {
  text-align: center;
}
.res-card > span > h4:first-child {
  display: flex;
  align-items: center;
  background-color: rgb(19, 176, 1);
  border-radius: 5px;
  padding: 0 5px 0;
  color: white;
}

.res-card > span > h4:first-child .fa-star {
  font-size: small;
  padding: 0 5px 3px 0;
}
.res-container {
  display: flex;
  flex-wrap: wrap;
}
.biryani {
  height: 180px;
  width: 100%;
}
